.profile__overlay {
    position: relative;
    cursor: pointer;
}

.profile__overlay::after {
    content: url('../../../images/vector.svg');
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    top: 0;
    right: 10px;
    opacity: 0;
    transition: opacity .5s ease-in-out 0s; 
}

.profile__overlay:hover::after {
    opacity: 1;
}